.root {
    height: 100%;
    background-color: #fff;
}

.navHeader {
    background-color: #fff;
}

.backHome {
    margin-top: 30px;
    text-align: center;
}

.backHome :global(a) {
    color: #019cda;
}

.form {
    margin-top: 50px;
}
.formItem {
    margin-top: 40px;
    position: relative;
}

.input {
    height: 50px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #333;
    outline-style: none;
    font-size: 18px;
    color: #333;
    border-radius: 25px;
}
.input:focus {
    border: 1px solid #00a16c;
    color: #00a16c;
}

.submit {
    width: 100%;
    height: 50px;
    margin-top: 40px;
    background-color: #00a16c;
    border: none;
    color: #fff;
    font-size: 22px;
    text-align: center;
    line-height: 50px;
    border-radius: 25px;
}
.error {
    position: absolute;
    height: 20px;
    margin-top: 5px;
    padding-left: 20Px;
    font-size: 14px;
    color: #ff0000;
    line-height: 20px;
}
